import { Component } from '@angular/core';
import { CrudService } from 'src/app/services/crud.service';
import { cusRequestUrl } from 'src/app/services/customerReqUrl.const';

@Component({
  selector: 'app-evc',
  templateUrl: './evc.component.html',
  styleUrls: ['./evc.component.scss']
})
export class EvcComponent {
  count : any
  evc:any=[
    {'name':'Bike','credit':'50','date':'18 May 2023'},
    {'name':'Car','credit':'150','date':'12 May 2023'},
    {'name':'Bike','credit':'150','date':'16 May 2023'},
    {'name':'car','credit':'100','date':'18 May 2023'},
    {'name':'Bike','credit':'150','date':'15 May 2023'},
    {'name':'Activa','credit':'100','date':'18 May 2023'},
   
  ];
  constructor(private crud: CrudService){
    this.crud.getRequest(cusRequestUrl.getEVCdetail).then((res:any)=>{
      this.evc = res.data.data;
      
    })
  }
  viewSelectedEVC(i:number){
    this.count = i;
  }
}
